@font-face {
  font-family: "SF Pro Black";
  font-style: black;
  font-weight: 400;
  src: url("./fonts/SFPro/SF-Pro-Display-Black.otf");
}

body {
  background: url(./img/background.jpg) no-repeat;
  background-size: cover;
  color: #ffffff;
}

body>#root>div>div {
  height: 98vh;
}

/* Title Header Styling */
.Title-header-left {
  margin-left: 2.5%;
  font-size: 200%;
}

/* Title Label Left Styling */
.Title-label-left {
  font-family: "SF Pro Black";
}

/* App Description Styling */
.App-description {
  margin-left: 2.5%;
  font-size: 200%;
}

h1,
p {
  color: #ffffff;
}