@font-face {
  font-family: 'SF Pro';
  font-style: normal;
  font-weight: 400;
  src: url(/static/media/SF-Pro-Display-Regular.aaeac71d.otf);
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "SF Pro", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body > #root > div {
  height: 98vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body > #root > div {
  height: 98vh;
}

.blocky {
  border-radius: 50%;
}
@font-face {
  font-family: "SF Pro Black";
  font-style: black;
  font-weight: 400;
  src: url(/static/media/SF-Pro-Display-Black.11e421ee.otf);
}

body {
  background: url(/static/media/background.e2e51dcc.jpg) no-repeat;
  background-size: cover;
  color: #ffffff;
}

body>#root>div>div {
  height: 98vh;
}

/* Title Header Styling */
.Title-header-left {
  margin-left: 2.5%;
  font-size: 200%;
}

/* Title Label Left Styling */
.Title-label-left {
  font-family: "SF Pro Black";
}

/* App Description Styling */
.App-description {
  margin-left: 2.5%;
  font-size: 200%;
}

h1,
p {
  color: #ffffff;
}
